import React, { useEffect, useMemo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { GET_ADD_FEED } from '../../../../../actions/moderationActions';
import { GET_LOCAL, GET_NETWORK_ICON, GET_NETWORK_NAME, SET_LOCAL, generateRandomArray, shopifyApp } from '../../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Image, Badge, Container, Card, Button } from 'react-bootstrap';
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC';
import ContactModal from '../../PricePlan/Elements/ContactModal';
import { checkPermission } from '../../../../../actions/collaboratorActions';
import SubHeaderCrumbs from '../../CommonComponents/SubHeader';
import UpgradePlan from '../UpgradePlan';
import PaymentModal from '../../PricePlan/Elements/PaymentModal';
import { getPlan, handleShopifySubscription } from '../../../../../actions/planDetail';
import { firstPaidPlan, PRODUCT_PLANS } from '../../../../../actions/constants';
import { updateSalesModal } from '../../../../../actions/loginActions';
import { GET_FEEDS } from '../../../../../actions/manageFeedsAction';
import { HUBSPOT_URL } from '../../../../../actions/api';
import SideMenu from '../../Moderation/SideMenu';

const AddFeed = () => {
    const { wallId } = useParams();
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const addFeedData = useSelector(state => state.addFeedReducer)
    const userData = useSelector(state => state.authorized?.userData?.user_data)
    const allWalls = useSelector(state => state?.dashboardData?.dashboarddata?.allWalls)
    const feeds = useSelector(state => state?.manageFeeds?.feeds);
    const [showContactUs, setShowContactUs] = useState(false)
    const [type, setType] = useState("")
    const [payment, setPayment] = useState(false)
    const [filterNetwork, setFilterNetwork] = useState([])
    const [network, setNetwork] = useState(null);

    const pageContent = useMemo(() => window?.APP_CONTENT?.add_feed ?? {}, [window?.APP_CONTENT])

    useEffect(() => {
        if (!feeds) dispatch(GET_FEEDS());
        dispatch(GET_ADD_FEED(undefined, true))
    }, [])

    const getLinkProps = (networkId, access) => {
        if (access == 0) return {}
        if (networkId == 45 || networkId == 46) return {};
        if (networkId === 33) return { to: "/creators" };
        if (networkId === 0) return { to: "/manual-upload" };
        return { to: `/content/addfeed/${wallId}/${networkId}` };
    };

    const clickToshowContact = (status, key) => {
        setShowContactUs(status)
        setType(key)
    }

    const checkTour = async (sel) => {
        const tourStep = GET_LOCAL('tourStep');
        const isTourComplete = GET_LOCAL('isTour') ?? true;
        const { default: appTour } = await import('../../../../../actions/appTour')
        const { tour } = new appTour(navigate, false, dispatch);
        if (!isTourComplete && addFeedData?.addFeedNetworks?.networks?.length) {
            if (tourStep?.id == '#tour_net_18') {
                SET_LOCAL('tourStep', { id: "#create_feed", step: 2 });
                if (sel) tour.destroy();
                else setTimeout(() => tour.highlight({ element: '#tour_net_18' }), 400);
            } else if (tourStep?.id == "#add_feed_container" && tourStep?.step == 1) setTimeout(() => tour.drive(sel ? 2 : 1), 400);
        }
    }

    const getPaidPlan = (type, loadingCB) => {
        const postBody = {
            billingcycle: "0",
            isAddOn: "0",
            couponCode: "",
            widget_addon_qty: "0",
            feeds_addon_qty: "0",
            twitter_addon_qty: "0",
            isCreatenewSubscription: false,
            planId: PRODUCT_PLANS.firstPaid
        }
        getPlan(postBody, type, loadingCB, userData?.sessionData?.createFrom == "bigcommerce");
    }

    const paymentMethod = () => {
        if (userData?.sessionData?.createFrom == "shopify") handleShopifySubscription(firstPaidPlan, false);
        else setPayment(true);
    }

    const clickNetwork = (network) => {
        if (network.access == 0 && network.id != 45 && network.id != 46) setNetwork(network);
        else if ([34, 35].includes(network.id)) clickToshowContact(true, network.name);
        else if ([45, 46].includes(network.id)) dispatch(updateSalesModal({ url: `${HUBSPOT_URL}`, embedCode: null }));
        //else if(localStorage.getItem('tourStep')) checkTour(true)
    }

    useEffect(() => {
        //checkTour();
        setFilterNetwork(addFeedData?.addFeedNetworks?.networks);
    }, [addFeedData?.addFeedNetworks?.networks])

    const renderNetworkBlocks = (network) => {
        return <div key={network.id} className="px-2 mb-4">
            <Link id={`tour_net_${network.id}`} className='btn btn-secondary position-relative border-0' {...getLinkProps(network.id, network.access)} onClick={() => clickNetwork(network)} title={GET_NETWORK_NAME(network.id)}>
                <div className="d-flex justify-content-center flex-column h-100">
                    {/* {network.id == 29 ?
                        <FontAwesomeIcon type={'solid'} icon={'star'} iconClass={'text-warning mb-2 mx-auto'} />
                        : */}
                    <>
                        <Image height={42} width={42} className='mb-2 mx-auto' src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg`)} alt={GET_NETWORK_NAME(network.id)} title={GET_NETWORK_NAME(network.id)} fluid />
                        {network.access == 0 ? <Image style={{ position: "absolute", right: '8px', top: '0', maxHeight: '18px' }} src={baseUrl(`media/icons/social-icons-square/premium.svg`)} /> : null}
                    </>
                    {/* } */}
                    <span className="fs-8">{network.name}</span>
                </div>
                {network.id === 33 && <Badge bg="" className='position-absolute badge-primary py-1' style={{ top: -16, right: 10 }}>{pageContent?.new ?? ""}</Badge>}
            </Link>
        </div >
    }

    const getWallData = allWalls?.find((e) => e.id == localStorage.getItem('wallId')) ?? {};
    const isShopify = shopifyApp();

    const filterAction = (status) => {
        let networkList = addFeedData?.addFeedNetworks?.networks;

        if (networkList && status) {
            let isReview = true;
            if (status == "social") isReview = false;
            networkList = networkList.filter(item => {
                if (item.id == 3 || item.isReview == isReview) return item;
            });
        }
        setNetwork(status);
        setFilterNetwork(networkList);
    }
    const breadCrumbs = window?.APP_CONTENT?.['sideBar'];

    return (<>
        {allWalls?.length ? <SubHeaderCrumbs key={getWallData?.id} crumbs={[{ name: getWallData?.name ?? "", icon: "grid-2-plus", imgUrl: getWallData?.thumbnail ?? "" }, { name: breadCrumbs?.home, link: "/home" }, { name: breadCrumbs?.contentGallery, link: "/content" }, { name: getWallData?.name, link: `/content/moderation/${getWallData?.id}` }, { name: breadCrumbs?.addFeed, link: `/content/addfeed/${getWallData?.id || localStorage.getItem('wallId') || ''}` }]} RightComponent={<div>
            {
                <button onClick={() => checkPermission('networkManager', 'manageFeed') ? navigate(`/content/managefeed/${localStorage.getItem('wallId')}`) : {}} className="btn btn-primary btn-md text-nowrap btn-min-w text-capitalize">
                    Manage feed
                </button>
            }
        </div>} showGallerySwitcher={false} /> : null}
        <div className={`custom-side-menu d-flex ${isShopify ? "isShopify" : ""}`}>
            <SideMenu {...{ getWallData }} />
            <div className="custom-main-content">
                <div className='d-flex flex-column flex-column-fluid full-content mt-10 mt-lg-0'>
                    <Container fluid className={isShopify ? "" : 'container-choose-network'}>
                        {/* <Card> */}
                        {/* <Card.Header className='f-between d-flex'>
                        <h2 className="me-2 h4 my-3">Add feeds</h2>
                        <div className="f-center">
                            {manageFeed.feeds?.length > 0 ? <Link to={`/content/managefeed/${localStorage.getItem('wallId')}`} className="btn btn-primary btn-sm text-nowrap btn-min-w" title='feed'>
                                Manage feeds
                            </Link> : null}
                        </div>
                    </Card.Header> */}
                        {/* <Card.Body> */}
                        {/* <div onClick={() => navigate('/content')} style={{ right: '15px', top: '10px', zIndex: '9', height: '20px', width: '20px', display: 'flex', alignItems: 'center', justifyContent: 'center' }} className="position-absolute cursor-pointer rounded-2">
                                    <FontAwesomeIcon icon="xmark" iconClass="cursor-pointer" />
                                </div> */}
                        <div className=''>
                            <div id="add_feed_container" className='f-center flex-column mb-0'>
                                <div className="d-flex justify-content-between position-relative w-100">
                                    <div className="mb-sm-3 mb-lg-5 mb-xl-5 mb-md-4 mb-3">
                                        <h3 className='text-capitalize cont-title fs-5'>{pageContent?.title_header ?? ""}</h3>
                                        <p className="text-muted">{pageContent?.desc_header ?? ""}</p>
                                    </div>
                                    <div onClick={() => navigate(`/content/moderation/${localStorage.getItem('wallId')}`)} style={{ right: '0', top: '10px', zIndex: '8', height: '20px', width: '20px', display: 'flex', alignItems: 'center', justifyContent: 'center' }} className="position-absolute cursor-pointer rounded-2 text-muted">
                                        <FontAwesomeIcon icon="xmark" iconClass="cursor-pointer fs-3" />
                                    </div>
                                </div>

                                {/* Filter networks start */}
                                {filterNetwork?.length > 0 ? <div className="network-filter tb_filter_round_edged_wrapper w-100">
                                    <div className="row">
                                        <div className="col-12 d-flex flex-row align-items-center justify-content-between mb-5 pb-3">
                                            <div className="filer-custom-desgin d-flex border-bottom w-100">
                                                <div className={`d-flex align-items-center jsutify-content-center`} >
                                                    <div className={`d-flex align-items-center`}>
                                                        <Button variant="secondary" size="md" className={`rounded-0 border-0 text-muted ${network === null ? 'active' : ''}`} onClick={() => filterAction(null)}>{pageContent?.btn_all_network ?? ""}</Button>
                                                    </div>
                                                </div>
                                                <div className={`d-flex align-items-center jsutify-content-center`} >
                                                    <div className={`d-flex align-items-center`}>
                                                        <Button variant="secondary" size="md" className={`rounded-0 border-0 text-muted ${network === 'social' ? 'active' : ''}`} onClick={() => filterAction('social')}><FontAwesomeIcon type={'solid'} size={14} icon="grid-2" /> {pageContent?.btn_social_network ?? ""}</Button>
                                                    </div>
                                                </div>
                                                <div className={`d-flex align-items-center jsutify-content-center`} >
                                                    <div className={`d-flex align-items-center`}>
                                                        <Button variant="secondary" size="md" className={`rounded-0 border-0 text-muted ${network === 'review' ? 'active' : ''}`} onClick={() => filterAction('review')}><FontAwesomeIcon type={'solid'} size={14} icon="star" /> {pageContent?.btn_review_network ?? ""}</Button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div> : null}
                                {/* Filter networks end */}

                                <div className="f-center flex-wrap mb-0 custom-network-box justify-content-start w-100">
                                    
                                    {filterNetwork?.length > 0 ?
                                        filterNetwork.map(renderNetworkBlocks)
                                        : generateRandomArray(18).map((_, index) => <div key={index} className="px-2 mb-4"><div className="sk_rect h-100px me-2 btn-min-w" style={{ width: 120 }}></div></div>)}

                                    {filterNetwork?.length > 0 ? <div className='px-2 mb-4'>
                                        <RequestButtonHOC title={pageContent?.request_network_title ?? ""}>
                                            <Button variant='secondary' className="custom-req-network border-0 px-2">
                                                <FontAwesomeIcon icon={'plus'} size={24} iconClass={'bg-primary color-white rounded-2 me-0 mb-2'} />
                                                <span className="d-block fs-8">{pageContent?.request_network_title ?? ""}</span>
                                            </Button>
                                        </RequestButtonHOC>
                                    </div> : null}
                                </div>
                            </div>
                        </div>
                        {/* </Card.Body> */}
                        {/* </Card> */}
                    </Container>
                    <ContactModal show={showContactUs} type={type} onCloseModel={() => clickToshowContact(false, "")} />

                    {network?.id == 0 || network?.id? <UpgradePlan network={network} showPayment={paymentMethod} onHide={() => setNetwork(null)} showPricing={() => navigate(`/price`)} /> : null}
                    <PaymentModal show={payment} onHide={() => setPayment(false)} paymentAction={(type, loadingCallBack) => getPaidPlan(type, loadingCallBack)} />
                </div>
            </div>
        </div>
    </>)
}

export default AddFeed