import React from 'react'
import { Modal, Image } from 'react-bootstrap'
import { formatTemplateText, GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../utils';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { firstPaidPlan } from '../../../../../actions/constants';
import './upgradeplan.scss';


export default function UpgradePlan({ network, onHide, showPayment, showPricing }) {
    const name = GET_NETWORK_NAME(network?.id);
    const icon = GET_NETWORK_ICON(network?.id);
    const price = firstPaidPlan.planPrice;
    const pageContent = window?.APP_CONTENT?.upgrade_plan ?? {};

    return (
        <Modal show={true} className="px-3 modal-lg" onHide={onHide} backdrop="static" keyboard={false} aria-labelledby="contained-modal-title-vcenter" centered>
            <Modal.Body className="p-0">
                <div className="popup-wrap">
                    <div className="popup-inn" style={{ maxWidth: "100%" }}>
                        <div className="inappBody">
                            <div className="planTitle d-flex align-items text-md-start text-center align-items-center position-relative">
                                <Image src={baseUrl(`media/icons/social-icons-square/${icon}.svg`)} alt={name} className="img-fluid me-3" />
                                <div className="planTitleinn">
                                    <strong>{formatTemplateText(pageContent?.modal_title ?? "", { network_name: name })}</strong>
                                    <p className="m-0">{formatTemplateText(pageContent?.modal_desc ?? "", { network_name: name })}</p>
                                </div>
                                <span onClick={onHide} className="popupClose"><Image src={baseUrl('media/icons/svg-icons/close-gray.svg')} alt="close" /></span>
                            </div>
                            <div className="planAll d-flex align-items-center justify-content-between flex-wrap">
                                <div className="planFeatures">
                                    <ul className="m-0 p-0 text-start">
                                        <li><span>{pageContent?.features ?? ""}</span></li>
                                        <li>{formatTemplateText(pageContent?.integration ?? "", { network_name: name })}</li>
                                        <li>{pageContent?.feeds ?? ""}</li>
                                        <li>{pageContent?.views ?? ""}</li>
                                        <li>{pageContent?.branding ?? ""}</li>
                                        <li>{pageContent?.customer_support ?? ""}</li>
                                    </ul>
                                </div>
                                <div className="planFree">
                                    <ul className="m-0 p-0 text-center">
                                        <li><Image src={baseUrl(`media/icons/svg-icons/check-gray.svg`)} alt="check" className="img-fluid" /><span className="d-block">{pageContent?.plans?.free?.title ?? ""}</span></li>
                                        {pageContent?.plans?.free?.items?.map((item, index) => (
                                            <li key={index}>{item}</li>
                                        ))}
                                    </ul>
                                </div>
                                <div className="planLite">
                                    <ul className="m-0 p-0 text-center">
                                        <li className="bg-primary text-white"><Image src={baseUrl(`media/icons/svg-icons/liteplan.svg`)} alt="liteplan" className="img-fluid" /><span className="d-block">{pageContent?.plans?.starter?.title ?? ""}</span></li>
                                        {pageContent?.plans?.starter?.items?.map((item, index) => (
                                            <li key={index}>{item}</li>
                                        ))}
                                        <li><button className="btn btn-primary w-100" onClick={showPayment}>{formatTemplateText(pageContent?.plans?.starter?.btn_upgrade ?? "", { price })}</button></li>
                                    </ul>
                                </div>
                            </div>
                            <div className="text-center seeAllplan">
                                <span className="d-inline-block cursor-pointer" onClick={showPricing}>{pageContent?.see_all_plans ?? ""}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal.Body>
        </Modal>)
}